@import url('https://fonts.googleapis.com/css2?family=Montserrat+Subrayada&family=Raleway:wght@100;200;300;400;500;600;700&display=swap');

body {
    margin: 0;
    padding: 0;
}

#root {
    padding: 0 20px;
}

.app {
    max-width: 960px;
    margin: 30px auto;
    padding: 60px 15px 20px;
    border: 1px solid lightgray;
    border-radius: 10px;
    position: relative;
}

.how-to-button {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 30px;
    width: 100px;
    border-radius: 17px;
    border: 1px solid lightgray;
    outline: none;
    color: gray;
    background-color: white;
    font-family: 'Raleway', sans-serif;
}

.how-to-button:hover {
    background-color: #E9E9E9;
    border: none;
    cursor: pointer;
}

.manual {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.invisible {
    display: none;
}

.manual__content {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    overflow: scroll;
    background-color: white;
    border-radius: 5px;
    max-width: 600px;
    height: 80%;
    padding-top: 10px;
    margin: auto;
}

.manual__text {
    width: 80%;
    text-align: left;
    font-family: 'Raleway', sans-serif;
    font-size: 13px;
    line-height: 18px;
}

.manual__text li {
    margin-bottom: 10px;
    padding-left: 7px;
}

.manual__close {
    font-size: 20px;
    font-weight: bold;
    color: grey;
    background-color: transparent;
    border: none;
    outline: none;
}

.manual__close:hover {
    color: #404040;
    cursor: pointer; 
}

.header {
    text-align: center;
    margin-bottom: 60px;
}

.header__heading {
    font-family: 'Montserrat Subrayada', sans-serif;
    color: #404040;
    margin: 0 0 20px;
}

.header__slogan {
    font-family: 'Raleway', sans-serif;
    color: grey;
    margin: 0 0 40px;
}

.header__wordfield {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__correct {
    color: green;
    font-size: 30px;
    margin: 0;
}

.header__wrong {
    color: red;
    font-size: 30px;
    margin: 0;
}

.header__input {
    height: 30px;
    width: 280px;
    font-size: 25px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid grey;
    outline: none;
    text-align: center;
    text-transform: uppercase;
    margin: 0 20px;
    font-family: 'Raleway', sans-serif;
}

.header__button {
    width: 90px;
    height: 34px;
    border-radius: 17px;
    border: none;
    color: white;
    background-color: grey;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-size: 16px;
}

.header__button:hover {
    cursor: pointer;
    opacity: 0.8;
}

.header__button:disabled {
    cursor: default;
    opacity: 0.4;
}

.blocks {
    display: flex;
    justify-content: space-evenly;
    margin: 0 0 50px;
}

.playerblock {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    box-sizing: border-box;
    width: 250px;
    height: 250px;
}

.playerblock__button {
    height: 36px;
    border-radius: 20px;
    border: none;
    margin: 10px 0;
    padding: 0 20px;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    color: white;
}

.playerblock__button:hover {
    cursor: pointer;
    opacity: 0.8;
}

.playerblock__button:disabled {
    cursor: default;
    opacity: 0.4;
}

.final-message {
    font-family: 'Raleway', sans-serif;
    color: #404040;
    text-align: center;
    font-size: 28px;
}

.final-buttons {
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
}

.nextround-button {
    /* margin: 0 calc((100% - 150px)/2) 30px; */
    margin-bottom: 30px;
    width: 150px;
    height: 38px;
    border-radius: 19px;
    border: none;
    color: white;
    background-color: #404040;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-size: 16px;
}

.nextround-button:hover {
    cursor: pointer;
    opacity: 0.8;
}

.red-block {
    background-color: #FFF1F1;
}

.green-block {
    background-color: #F1FFF4;
}

.red-score {
    color: #ac3535;
    font-family: 'Raleway', sans-serif;
    font-size: 45px;
    margin: 30px 0;
}

.green-score {
    color: #4a8352;
    font-family: 'Raleway', sans-serif;
    font-size: 45px;
    margin: 30px 0;
}

.red-buttons {
    background-color: #ac3535;
}

.green-buttons {
    background-color: #4a8352;
}

.not-selected {
    opacity: 0.4;
}

.red-color {
    color: #ac3535;
}

.green-color {
    color: #4a8352;
}

.yandex {
    font-family: 'Raleway', sans-serif;
    color: grey;
    text-align: right;
    padding-right: 40px;
}

.yandex__link {
    color: #404040;
}

@media screen and (min-width: 320px) and (max-width: 650px) {
    .header__slogan {
        padding: 0 10px;
    }

    .header__wordfield {
        flex-wrap: wrap;
    }

    .header__input {
        width: 240px;
        margin-bottom: 10px;
    }

    .playerblock__button {
        padding: 0 12px;
        font-size: 14px;
    }

    .how-to-button {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .final-buttons {
        width: 90%;
    }

    .nextround-button {
        font-size: 14px;
        width: 120px;
    }
}